






















import { ref, watch } from '../../../../../adapter-vue';
import { IMessageModel, TUITranslateService } from '@tencentcloud/chat-uikit-engine';
import { convertor } from '../../../utils/convertVoiceToText';
interface IProps {
  message: IMessageModel;
  contentVisible: boolean;
}
interface IEmits {
  (e: 'toggleErrorStatus', status: boolean): void;
}
const __sfc_main = {};
__sfc_main.props = {
  message: {
    key: "message",
    required: false,
    type: null,
    default: () => ({} as IMessageModel)
  },
  contentVisible: {
    key: "contentVisible",
    required: true,
    type: Boolean
  }
};
__sfc_main.setup = (__props, __ctx) => {
  const emits = __ctx.emit;
  const props = __props;
  const convertFinished = ref<boolean>(false);
